import Image from "next/image";
import React from "react";
import Local from "next/font/local";
import { TiHtml5 } from "react-icons/ti";
import { FaCss3Alt } from "react-icons/fa";
import { FaJsSquare } from "react-icons/fa";
import { FaReact } from "react-icons/fa";
import { BiLogoTypescript } from "react-icons/bi";
import { RiNextjsFill } from "react-icons/ri";
import { RiTailwindCssFill } from "react-icons/ri";
import { FaNodeJs } from "react-icons/fa6";
import { PiFileCSharpBold } from "react-icons/pi";
import { FaJava } from "react-icons/fa";
import { FiFramer } from "react-icons/fi";

const Potato_sans = Local({
  src: [
    {
      path: "../../../public/fonts/Potato_sans-Black.otf",
      weight: "400",
      style: "normal",
    },
  ],
  variable: "--font-potato_sans",
  display: "swap",
});

const wi = Local({
  src: [
    {
      path: "../../../public/fonts/Wigglye.ttf",
      weight: "500",
      style: "normal",
    },
  ],
  variable: "--font-super-marker",
  display: "swap",
});

const SkillsHeading =
  "https://res.cloudinary.com/dmmzqpfgg/image/upload/v1749314829/Picsart_25-06-07_18-51-05-546_ktohmh.png";

const SkillsDescription =
  "https://res.cloudinary.com/dmmzqpfgg/image/upload/v1749367137/skills-croped_feipdy.png";

const MySkills = () => {
  return (
    <div className="relative py-3 w-full ">
      <div className="relative third-image h-52 w-full flex items-center justify-center">
        <h1
          className={`absolute third-h1 text-[3.2rem] text-red-700 mt-2 -rotate-3 text-shadow-yellow-300 text-shadow-sm z-20 ${Potato_sans.className}`}>
          My Skills{" "}
        </h1>
        <Image
          src={SkillsHeading}
          alt="comic type heading"
          fill
          className="relative"
        />
      </div>
      <div className="relative h-52 w-full flex items-center justify-center">
        <p
          className={`h-26 -rotate-1 ml-5 w-[94%] text-[#ECE852] text-shadow-sm text-shadow-black tracking-wider flex flex-wrap gap-2 z-20 ${SuperMarker.className}`}>
          <span className="flex flex-col items-center">
            <TiHtml5 />
            <p>HTML</p>
          </span>
          <span className="flex flex-col items-center">
            <FaCss3Alt />
            <p>CSS</p>
          </span>
          <span className="flex flex-col items-center">
            <FaJsSquare />
            <p>JavaScript</p>
          </span>
          <span className="flex flex-col items-center">
            <FaReact />
            <p>React</p>
          </span>
          <span className="flex flex-col items-center">
            <BiLogoTypescript />
            <p>Typescript</p>
          </span>
          <br />
          <span className="flex flex-col items-center">
            <RiNextjsFill />
            <p>Next</p>
          </span>
          <span className="flex flex-col items-center">
            <RiTailwindCssFill />
            <p>Tailwind css</p>
          </span>
          <span className="flex flex-col items-center">
            <FaNodeJs />
            <p>Nodejs</p>
          </span>
          <span className="flex flex-col items-center">
            <PiFileCSharpBold />
            <p>C#</p>
          </span>
          <span className="flex flex-col items-center">
            <FaJava />
            <p>Java</p>
          </span>
          <span className="flex flex-col items-center">
            <FiFramer />
            <p>Framer Motion</p>
          </span>
          <span className="flex flex-col items-center">
            <PiFileCSharpBold />
            <p>C#</p>
          </span>
        </p>
        <Image src={SkillsDescription} alt="Skills" fill className="relative" />
      </div>
    </div>
  );
};

export default MySkills;
